import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AdCarouselComponent } from './ad-carousel/ad-carousel.component';
import { AdFooterComponent } from './ad-footer/ad-footer.component';
import { AdNavbarComponent } from './ad-navbar/ad-navbar.component';
import { AdProductItemComponent } from './ad-product-item/ad-product-item.component';
import { AdStarsComponent } from './ad-stars/ad-stars.component';
import { AdSearchComponent } from './ad-search/ad-search.component';
import { AdBootstrapComponent } from './ad-bootstrap.component';
import {RouterModule, Routes} from "@angular/router";
import {AdServiceModule} from "./service/ad-service.module";
import {AdHomeComponent} from "./ad-home/ad-home.component";
import { AdProductDetailComponent } from './ad-product-detail/ad-product-detail.component';
import {SharedModule} from "../shared/shared.module";
import {FormsModule, ReactiveFormsModule} from "@angular/forms";

const routes: Routes = [
  {
    path: 'angular-developer', component: AdBootstrapComponent,
    children: [
      {path: '', component: AdHomeComponent},
      {path: 'product', component: AdProductItemComponent},
      {path: 'product/:id', component: AdProductDetailComponent}
    ]
  }
];

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    SharedModule,
    AdServiceModule,
    RouterModule.forChild(routes)
  ],
  declarations: [
    AdCarouselComponent,
    AdFooterComponent,
    AdNavbarComponent,
    AdProductItemComponent,
    AdStarsComponent,
    AdSearchComponent,
    AdBootstrapComponent,
    AdHomeComponent,
    AdProductDetailComponent,
  ]
})
export class AngularDeveloperModule { }



