import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { UmBootstrapComponent } from './um-bootstrap/um-bootstrap.component';
import {SharedModule} from "../shared/shared.module";
import { UmSignupComponent } from './um-bootstrap/um-signup/um-signup.component';
import { UmLoginComponent } from './um-bootstrap/um-login/um-login.component';
import { UmTrainingComponent } from './um-bootstrap/um-training/um-training.component';
import { UmCurrentTrainingComponent } from './um-bootstrap/um-current-training/um-current-training.component';
import { UmPastTrainingComponent } from './um-bootstrap/um-past-training/um-past-training.component';
import { UmWelcomeComponent } from './um-bootstrap/um-welcome/um-welcome.component';
import {UdemyMaterialRoutingModule} from "./um-routing.module";
import {FormsModule} from "@angular/forms";
import {HttpModule} from "@angular/http";


@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    FormsModule,
    HttpModule,
    UdemyMaterialRoutingModule,
  ],
  declarations: [
    UmBootstrapComponent,
    UmSignupComponent,
    UmLoginComponent,
    UmTrainingComponent,
    UmCurrentTrainingComponent,
    UmPastTrainingComponent,
    UmWelcomeComponent
  ]
})
export class UdemyMaterialModule { }



