import {NgModule} from "@angular/core";
import {RouterModule, Routes} from "@angular/router";
import {UmBootstrapComponent} from "./um-bootstrap/um-bootstrap.component";
import {UmWelcomeComponent} from "./um-bootstrap/um-welcome/um-welcome.component";
import {UmSignupComponent} from "./um-bootstrap/um-signup/um-signup.component";
import {UmLoginComponent} from "./um-bootstrap/um-login/um-login.component";
import {UmTrainingComponent} from "./um-bootstrap/um-training/um-training.component";

const routes: Routes = [
  {path: 'udemy-material', component: UmBootstrapComponent, children: [
      {path: '', component: UmSignupComponent, pathMatch: 'full' },
      {path: 'signup', component: UmSignupComponent, },
      {path: 'login', component: UmLoginComponent, },
      {path: 'training', component: UmTrainingComponent, },
    ]}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class UdemyMaterialRoutingModule{}



