import {NgModule} from "@angular/core";
import {FlexLayoutModule} from "@angular/flex-layout";
import {
  MatButtonModule,
  MatCardModule,
  MatDialogModule,
  MatDividerModule,
  MatFormFieldModule,
  MatInputModule,
  MatToolbarModule,
  MatTooltipModule,
  MatListModule,
  MatSidenavModule,
  MatIconModule,
  MatExpansionModule,
  MatProgressSpinnerModule
} from "@angular/material";

const materials = [
  FlexLayoutModule,
  MatProgressSpinnerModule,
  MatDialogModule,
  MatCardModule,
  MatDividerModule,
  MatSidenavModule,
  MatToolbarModule,
  MatTooltipModule,
  MatListModule,
  MatIconModule,
  MatExpansionModule,
  MatButtonModule,
  MatFormFieldModule,
  MatInputModule,
];

@NgModule({
  imports: materials,
  exports: materials,
})
export class MaterialModule{}



