import {Component, Input, OnInit} from '@angular/core';

@Component({
  selector: 'sw-ad-stars',
  templateUrl: './ad-stars.component.html',
  styleUrls: ['./ad-stars.component.css']
})
export class AdStarsComponent implements OnInit {
  @Input() rating: number;
  @Input() count: number = 5;
  stars: boolean[] = [];

  constructor() { }

  ngOnInit() {
    for(let i=1; i<=this.count; i++) {
      this.stars.push(i < this.rating);
    }
  }

}



