import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Params} from "@angular/router";
import {AdProductRepository} from "../service/ad-product-respository.service";
import {Product} from "../model/product.model";
import {Review} from "../model/review.model";
import {AdReviewRepository} from "../service/ad-review-repository.service";
import 'rxjs/Rx';
@Component({
  selector: 'app-ad-product-detail',
  templateUrl: './ad-product-detail.component.html',
  styleUrls: ['./ad-product-detail.component.css']
})
export class AdProductDetailComponent implements OnInit {
  product: Product;
  reviews: Review[];

  constructor(private route: ActivatedRoute,
              private productRepository: AdProductRepository,
              private reviewRepository: AdReviewRepository) {

    this.route.params.subscribe((params: Params) => {
      let productId: number = parseInt(params['id']);
      this.productRepository.byId(productId)
        .subscribe(_product => {
          this.product = _product;
          this.reviewRepository.byProductId(this.product.id)
            .subscribe(_reviews => this.reviews = _reviews);
        });
    })
  }

  ngOnInit() {
  }

}



